
import {QrcodeReader} from 'vue-qrcode-reader'
import 'vue-qrcode-reader/dist/vue-qrcode-reader.css'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import axios from 'axios'
import { userInfoTran, friendInfoTran } from '../../utils/dataTran'
import { getItem } from '../../utils/storage'
export default {
data () {
return {
value: '',
show: false,
paused: false,
userInfo: {},
userId: getItem('user').id
}
},
components: {
QrcodeReader,
Group,
Cell,
CellBox,
XHeader,
XButton,
XDialog
},
methods: {
async onDecode (content) {
if (content) {
this.paused = true
const friendInfo = await this.getAddedFriends(content)
friendInfo.length === 0 ? this.userInfo = await this.getUserInfo(content) : this.userInfo = friendInfo
this.show = true
}
console.log(this.userInfo)
},
getAddedFriends (val) {
axios
.get(`/api/getfriendslist`, {
params: {
userId: this.userId,
val: val || '',
status: 0
}
})
.then((res) => {
const friendInfo = res.data.status && friendInfoTran(res.data.list)
return friendInfo
}).catch((error) => {
console.log(error)
})
},
getUserInfo (val) {
axios.get(`/api/getuserinfo`, {
params: {
friendId: val
}
})
.then((res) => {
const userInfo = res.data.status && userInfoTran(res.data.list, true)
return userInfo
}).catch((error) => {
console.log(error)
})
},
add (index) {
const body = {
userId: this.userId,
friendId: this.list[index].id
}
const _this = this
axios
.post(`/api/addfriend`, body)
.then((res) => {
if (res.data) {
AlertModule.show({
title: '请求已发送！',
onHide () {
_this.getAddedFriends()
}
})
}
}).catch((error) => {
console.log(error)
})
}
}
}



